import React, { Component } from 'react';

class Project extends Component {
    state = {
        showinfo: false
    }
    handeleInfo = () => {
        this.setState({
            showinfo :! this.state.showinfo
        })
    }
    
    render() {
       let {name, languagesIcons, source, info, picture} = this.props.item

        return (
           <div className="project">
               <div className="icons">
                   {languagesIcons.map(icon =>  <i className={icon} key={icon}></i>)}
               </div>
               <h3>{name}</h3>
               <img src={picture} alt="" onClick={this.handeleInfo} />
               <span className="infos" onClick={this.handeleInfo}>
                   <i className="fas fa-plus-circle"></i>
               </span>
               {
                   this.state.showinfo && (
                       <div className="showInfos">
                           <div className="infosContent">
                               <div className="head">
                                   <h2>{name}</h2>
                                   <div className="sourceCode">
                                       <a href={source} rel="noopener noreferrer" className="button" target="_blank">Исходный код</a>
                                   </div>
                               </div>
                               <p className="text">{info}</p>
                               <div className="button return" onClick={this.handeleInfo}>Вернуться на страницу</div>
                           </div>
                       </div>
                   )
               }
           </div>
        );
    }
}

export default Project;