import React from 'react';
import { NavLink } from 'react-router-dom';

const Navigation = () => {
    return (
       <div className="sidebar">
          
           <div className="id">
               <div className="idContent">
                   <img src="./media/madjid.jpg" alt="profil-pic" width="10%"/>
                   <h3>Хоуфачи Маджид</h3>
               </div>
           </div>
           <div className="navigation">
               <ul> 
                   <li>
               <NavLink exact to="/" activeClassName="navActive">
                   <i className="fas fa-home"></i>
                   <span>Дом</span>
               </NavLink>
               </li>
                   <li>
               <NavLink exact to="/Skills"  activeClassName="navActive">
                   <i className="fas fa-mountain"></i>
                   <span>Навыки и умения</span>
               </NavLink>
               </li>
                   <li>
               <NavLink exact to="/portfolio" activeClassName="navActive">
                   <i className="fas fa-images"></i>
                   <span>портфолио</span>
               </NavLink>
               </li>
                   <li>
               <NavLink exact to="/contact" activeClassName="navActive">
                   <i className="fas fa-address-book"></i>
                   <span>Контакт</span>
               </NavLink>
               </li>
               </ul>
           </div>
           <div className="socialNetwork">
               <ul>
                   <li>
                       <a href="https://www.google.com" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin"></i></a>
                   </li>
                   <li>
                       <a href="https://github.com/many200/Itcompany" target="_blank" rel="noopener noreferrer"><i className="fab fa-github"></i></a>
                   </li>
                   <li>
                       <a href="https://www.instagram.com/many_k20/" target="_blank" rel="noopener noreferrer"><i className="fab fa-instagram"></i></a>
                   </li>
                   <li>
                       <a href="https://www.google.com" target="_blank" rel="noopener noreferrer"><i className="fab fa-facebook"></i></a>
                   </li>
               </ul>
               <div className="signature">
                   <p>Many23 -2021</p>
               </div>
           </div>
       </div>
    );
};

export default Navigation;