import React from 'react';
import { NavLink } from 'react-router-dom';

const Notfound = () => {
    return (
       
       <div className="notFound">
           <div className="notFoundContent">
               <h3>Извините, эта страница не существует !</h3>
               <NavLink exact to="/">
                   <i className="fas fa-home"></i>
                   <span>Дом</span>
               </NavLink>
           </div>
       </div>
       
    );
};

export default Notfound;