import React from 'react';

const Hobbies = () => {
    return (
      <div className="hobbies">
          <h3>Интерес</h3>
          <ul>
          <li className="hobby"><i className="fas fa-running"></i><span>Бег</span></li>
          <li className="hobby"><i className="fas fa-hiking"></i> <span>Поход</span></li>
          <li className="hobby"><i className="fas fa-seedling"></i><span>Культура</span></li>
          <li className="hobby"><i className="fab fa-bitcoin"></i><span>Крипто деньги</span></li>
          <li className="hobby"><i className="fas fa-rocket"></i><span>Космос </span></li>
          </ul>
      </div>
    );
};

export default Hobbies;