import React from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import Navigation from '../Components/Navigation';

const Contact = () => {
    return (
        <div className="contact">
          <Navigation /> 
            <div className="contactContent">
                <div className="header"></div>
                    <div className="contactBox">
                        <h1>Свяжитесь со мной</h1>
                        <ul>
                            <li>
                                <i className="fas fa-map-marker-alt"></i>
                                <span>Харько</span>
                            </li>
                            <li>
                                <i className="fas fa-mobile-alt"></i>
                                <CopyToClipboard text="+380633712545">
                                    <span className="clickInput" onClick={() => {alert("Номер скопирован!");}}>+38 06 33 71 25 45</span>
                                </CopyToClipboard>
                            </li>
                            <li>
                                <i className="far fa-envelope"></i>
                                <CopyToClipboard text="manyk436@gmail.com">
                                    <span className="clickInput" onClick={() => {alert("электронная почта скопирована!");}}>Manyk436@gmail.com</span>
                                </CopyToClipboard>
                            </li>
                        </ul>
                    </div>
                <div className="socialNetwork">
                    <ul>
                    
                       <a href="https://www.google.com" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin"></i></a>
                   
                       <a href="https://github.com/many200/Itcompany" target="_blank" rel="noopener noreferrer"><i className="fab fa-github"></i></a>
                  
                       <a href="https://www.instagram.com/many_k20/" target="_blank" rel="noopener noreferrer"><i className="fab fa-instagram"></i></a>
                  
                       <a href="https://www.google.com" target="_blank" rel="noopener noreferrer"><i className="fab fa-facebook"></i></a>
                  
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default Contact;